 function mainSlider() {
     var swiper = new Swiper('.main .swiper-container', {
         slidesPerView: 1,
         spaceBetween: 10,
         autoplay: true,
         effect: 'fade',
         pagination: {
             el: '.main .swiper-pagination',
             type: 'bullets',
             clickable: true,
         },
     })
 }

 function sponsorSlider() {
     var swiper = new Swiper('.sponsor .swiper-container', {
         slidesPerView: 5,
         spaceBetween: 30,
         navigation: {
             nextEl: '.sponsor .swiper-button-next',
             prevEl: '.sponsor .swiper-button-prev',
         },
         breakpoints: {
             320: {
                 slidesPerView: 1,
             },
             520: {
                 slidesPerView: 2,
             },
             768: {
                 slidesPerView: 3,
             },
             992: {
                 slidesPerView: 4,
             },
             1200: {
                 slidesPerView: 5,
             },
         }
     })
 }

 function serviceSlider() {
     var swiper = new Swiper('.service .swiper-container', {
         slidesPerView: 1,
         spaceBetween: 10,
         direction: "vertical",
         navigation: {
             nextEl: '.service .swiper-button-next',
             prevEl: '.service .swiper-button-prev',
         },
         pagination: {
             el: '.service .swiper-pagination',
             type: 'bullets',
             clickable: true,
             renderBullet: function(index, className) {
                 return `<span class="dot swiper-pagination-bullet">
                            <svg width="11" height="11" viewBox="0 0 11 11" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <circle cx="5.5" cy="5.5" r="1" stroke="white"/>
                            <circle cx="5.5" cy="5.5" r="5" stroke="white"/>
                            </svg>
                        </span>`;
             },
         },
     })
 }

