 function projectSlider() {
     var swiper = new Swiper('.projects-slider1 .swiper-container', {
         slidesPerView: 1,
         spaceBetween: 25,
         navigation: {
             nextEl: '.projects-slider1 .swiper-button-next',
             prevEl: '.projects-slider1 .swiper-button-prev',
         },
         pagination: {
             el: '.projects-slider1 .swiper-pagination',
             type: 'bullets',
             clickable: true,
         },
         breakpoints: {
             320: {
                 spaceBetween: 10,
             },
             1200: {
                 spaceBetween: 25,
             },
         },
         on: {
             init: function() {
                 this.slideTo(0);
             },
         },
     })
     $('.projects-slider1 .swiper-slide').click(function(e) {
         if (window.innerWidth > 992) {
             swiper.update();
             let slideIndex = $(this).attr("aria-label").split("/")
             swiper.slideTo(slideIndex[0] - 1);
             swiper.update();
         }

     });
 }

